<template>
  <div class="container mt-4">
    <h2 class="mb-4">주문 내역</h2>
    <div class="list-group">
      <a
        v-for="order in orders"
        :key="order.id"
        href="#"
        class="list-group-item list-group-item-action"
      >
        <h5 class="mb-1">상호명{{ order.sname }}</h5>
        <p class="mb-1">주문 번호: {{ order.onum }}</p>
        <p class="mb-1">요청사항: {{ order.omsg }}</p>
        <p class="mb-1">주문날짜: {{ order.odate }}</p>
        <p class="mb-1">주문상태: {{ order.odate }}</p>
        <!-- 주문에 대한 추가 정보 표시 -->
      </a>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue';
import axios from 'axios';

export default {
  name: 'OrderList',
  setup() {
    // 반응성 변수로 주문 목록을 저장합니다.
    const orders = ref([]);

    // 주문 목록을 가져오는 비동기 함수를 정의합니다.
    const fetchOrders = async () => {
      try {
        const response = await axios.post('/api/ordering', {
          params: {
            mnum: 1001 // mnum 값을 포함하여 요청합니다.
          }
        });
      // 가져온 데이터를 orders 변수에 할당합니다.
      orders.value = response.data;
      } catch (error) {
        console.error('Error fetching orders:', error);
      }
    };

    // 컴포넌트가 마운트된 후에 주문 목록을 가져오도록 합니다.
    onMounted(fetchOrders);

    // orders와 fetchOrders 함수를 반환합니다.
    return { orders, fetchOrders };
  },
};
</script>

<style scoped>
/* Additional styling can be added here if needed */
</style>
